.content_header {
  &_subtitle {
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    /* identical to box height, or 166% */

    color: #ffffff;
  }
  &_title {
    font-weight: 300;
    font-size: 38px;
    line-height: 48px;
    /* or 126% */
    color: #ffffff;
  }
  &_text {
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    /* or 160% */
    color: #ffffff;
  }
}
