*,
::after,
::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 0;
}

body {
  overflow-x: hidden;
  font-family: $fonts-main-page, sans-serif;
  color: $text-color-dark;
  background-color: $background-color-light;
}
h1,
h2,
h3,
p {
  margin-top: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  padding: 0;
  margin: 0;
}
.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 320px;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: $tablet) {
    max-width: $tablet;
    padding-left: 28px;
    padding-right: 28px;
  }

  @media screen and (min-width: $desktop) {
    max-width: $desktop;
  }
}
.title {
  display: flex;
  justify-content: flex-start;
  width: 200px;
  margin-top: 0;
  margin-bottom: 0;
  font-family: $fonts-main-page;
  font-weight: 400;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media screen and (min-width: $tablet) {
    min-width: 340px;
    font-size: 48px;
    line-height: 55px;
    letter-spacing: 0.06em;
  }

  @media screen and (min-width: $desktop) {
    width: 534px;
    font-size: 58px;
    line-height: 66px;
  }
}

.subtitle {
  margin-top: 0;
  margin-bottom: 17px;
  max-width: 232px;
  font-family: $fonts-main-page;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;

  @media screen and (min-width: $tablet) {
    max-width: 340px;
    margin-bottom: 16px;
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: $desktop) {
    max-width: 472px;
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 34px;
  }
}
